.post__card {
  transition: all 0.2s ease;
  background: white;
  border: solid 1px #979797;
  border-radius: 7px;
  margin: 2px 0; }
  .post__card:hover {
    background: #D8D8D8;
    cursor: pointer; }
  .post__card .titleRow {
    height: 16px;
    padding-top: 4px; }
    .post__card .titleRow .title, .post__card .titleRow .date {
      line-height: 16px;
      font-size: 16px; }
    .post__card .titleRow .title {
      padding-left: 5px;
      font-weight: 400;
      float: left; }
    .post__card .titleRow .date {
      padding-right: 5px;
      float: right; }
  .post__card .subtitle {
    padding: 6px;
    font-size: 14px;
    line-height: 14px;
    font-weight: 300;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden; }

